.cards {
  align-self: center;
  grid-area: 2/2;
}

.card {
  background-color: var(--verydarkblue-card-bg);
  color: var(--white);
  border-radius: 15px;
  /* Fix this (view port is not good) :  */
}
.card__image {
  width: 100%;
  padding: 20px;
}
.card__image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}
.card__name {
  color: var(--white);
  font-weight: 700;
  font-size: 25px;
  padding-left: 30px;
}
.card__descript {
  padding: 25px;
  font-size: 18px;
  font-weight: 300;
  color: var(--softblue);
}
.card__price {
  display: flex;
  justify-content: baseline;
  align-items: center;
  line-height: 1;
}
.card__price img {
  padding: 10px;
}
.card__number {
  color: var(--cyan);
}
.card__time {
  color: #000;
  position: relative;
  left: 56%;
  padding: 4px;
}
.card__time img {
  vertical-align: bottom;
  padding: 0;
  margin: 0;
}
.card__creator {
  border-top: 0.125rem solid var(--verydarkblue-line);
  margin: 20px;
  padding: 20px 0 20px 5px;
  display: flex;
  justify-content: baseline;
  align-items: center;
  color: var(--softblue);
}
.card__creator img {
  border: 3px solid var(--white);
  max-width: 100%;
  margin: 10px;
  border-radius: 50%;
}
.card__creator span {
  color: var(--white);
  padding-left: 5px;
  text-decoration: none;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  background: var(--verydarkblue-main-bg);
  font-family: var(--font-outfit);
}

:root {
  /* Primary Colors : */
  --softblue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  /* Neutral Colors : */
  --verydarkblue-main-bg: hsl(217, 54%, 16%);
  --verydarkblue-card-bg: hsl(217, 40%, 22%);
  --verydarkblue-line: hsl(215, 34%, 26%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-outfit: "Outfit", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
}
@media (min-width: 71.25em) {
  .container {
    padding: 0;
  }
}